.b-reviews {
  padding: 0 15px; }
  .b-reviews__items {
    max-width: 815px;
    margin: auto; }
  .b-reviews .owl-theme .owl-nav.disabled + .owl-dots {
    transform: translateY(-30px); }
  .b-reviews .owl-dots .owl-dot {
    border: 1px solid #eeeeee;
    margin: 4px;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    position: relative;
    background: #f7f7f8; }
  .b-reviews .owl-theme .owl-dots .owl-dot span {
    margin: 3px;
    background: transparent; }
  .b-reviews__top {
    margin: 0 -15px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin-right: 15px; }
  .b-reviews__new {
    margin-bottom: 30px; }

.b-review {
  padding: 60px 0 30px 0;
  text-align: center;
  margin: auto; }
  .b-review__face {
    position: relative;
    display: inline-block;
    border: 1px solid #e8e8e8;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 15px;
    max-width: 140px; }
  .b-review__img {
    padding: 5px;
    border-radius: 50%;
    max-width: 100%; }
  .b-review__message {
    padding: 15px 0; }
  .b-review__author {
    padding: 15px 0; }
  .b-review__author-name {
    font-weight: bold; }
  .b-review__author-job {
    color: #7aa104;
    font-size: 0.85714em;
    margin-top: 3px; }
  .b-review--line {
    text-align: left;
    white-space: nowrap;
    padding: 25px 0;
    border-top: 1px solid #d7dbdd;
    border-bottom: 1px solid #d7dbdd;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin: 0 -15px 25px -15px; }
    .b-review--line > * {
      white-space: normal; }
  .b-review--line .b-review__face,
  .b-review--line .b-review__content {
    display: inline-block;
    vertical-align: middle; }
  .b-review--line .b-review__face {
    margin: 15px 0;
    flex: 1 1;
    border: 2px solid #7aa104;
    max-width: 50%; }
  .b-review--line .b-review__content {
    flex: 1 1; }
  .b-review--line .b-review__author {
    padding: 0; }
  .b-review--line .b-review__message,
  .b-review--line .b-review__author-job {
    color: #676d72; }
  .b-review--line .b-review__message {
    font-style: italic; }
  @media (min-width: 480px) {
    .b-review--line {
      flex-direction: row;
      align-items: center; }
    .b-review--line .b-review__face {
      max-width: 140px;
      margin: 0;
      margin-right: 45px; } }
  @media (min-width: 992px) {
    .b-review--line .b-review__face {
      margin-left: 45px; } }
